import { BrowserRouter, Route, Switch,NavLink } from 'react-router-dom'
import React from 'react'
import App from './Apps'
function Nav(){
    function Extend(){
        document.getElementById('partOfMenu').style.overflow = 'visible'

    }
    function Close(){
        document.getElementById('partOfMenu').style.overflow = 'hidden'
    }
    return (
        <React.Fragment>
        <div id = 'menu'>
        <div id = 'heading'><h1 id = 'title'><a href='https://www.instagram.com/prashant_jhim/' target = '_'>Prashant Jhim</a></h1></div>
        <div id = 'NavC'>
        <NavLink id = 'part' exact to = '/'>Home</NavLink>
        <NavLink id = 'part' exact to = '/Pictures'>Pictures</NavLink>
        <NavLink id = 'part' exact to = '/contact'>Contact</NavLink>
        <NavLink id = 'partend' exact to = '/Search'>Search</NavLink></div>
        <div id = 'partOfMenu'>
        <button onClick = {Extend}>Menu</button>
        <NavLink onClick = {Close} id = 'part' exact to = '/'>Home</NavLink>
        <NavLink onClick = {Close} id = 'part' exact to = '/Pictures'>Pictures</NavLink>
        <NavLink onClick = {Close} id = 'part' exact to = '/contact'>Contact</NavLink>
        <NavLink onClick = {Close} id = 'partend' exact to = '/Search'>Search</NavLink></div>
        </div>
        </React.Fragment>
    )
}
export default Nav