import React, { useState } from 'react'

function Search(){
    let [img,search] = useState('')
    function AddSearch(){
        var Data = document.getElementById('inputData').value
        if (Data != ''){
            var Link = 'https://source.unsplash.com/400x400/?'
        var news = Link + Data
        console.log(news)
        search(news)
        }
    }
    
    return(
        <div id = 'search'>
        <input id = 'inputData' placeholder='Search Anything' onChange={AddSearch}></input>
        <img src={img}></img>
        
        </div>
    )
}
export default Search